/* App.css */

body {
  margin: 0; /* Remove default margin */
  font-family: 'Arial', sans-serif; /* Set a clean font */
}

.landing-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Prevent scrolling */
  position: relative;
  background-color: #0f0f0f; /* Background color */
  text-align: center; /* Center text */
}

.company-name {
  position: absolute;
  top: 20px; /* Position at the top */

  color: white; /* Text color */
  font-size: 2.5rem; /* Company name font size */
  opacity: 0; /* Start hidden */
  animation: fadeIn 1s forwards 2s; /* Fade in after animation */
  font-family: 'IBM Plex Mono', monospace; /* Apply the font */
}

.comingsoon{
  position: absolute;
  top: 110px; /* Position at the top */
  color: rgba(255, 255, 255, 0.639); /* Text color */
  font-size: 0.9rem; /* Company name font size */
  opacity: 0; /* Start hidden */
  animation: fadeIn 1s forwards 2s; /* Fade in after animation */
  font-family: 'IBM Plex Mono', monospace; /* Apply the font */
}


.tagline-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* Center the element */
  opacity: 0;
  animation: fadeIn 0.7s forwards; /* Fade in effect */
}



.contact-email {
  margin-top: 10px; /* Space above the email */
  color: rgba(255, 187, 91, 0.856); /* Text color */
  font-size: 1rem; /* Font size */
  opacity: 0; /* Start hidden */
  animation: fadeIn 1s forwards 2s; /* Fade in after brewing text */
}


@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.tagline {
  font-size: 2rem; /* Initial font size */
  color: rgba(255, 255, 255, 0.887); /* Text color */
  font-family: 'IBM Plex Mono', monospace; /* Apply the font */

}

.tagline-2 {
  font-size: 1.5rem; /* Initial font size */
  color: rgba(255, 255, 255, 0.758); /* Text color */
  font-family: 'IBM Plex Mono', monospace; /* Apply the font */

}

.fade-out {
  animation: fadeOut 1s forwards; 
  transform: translate(-50%, -50%); 
}


@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate(-50%, -50%); 
  }
}
.animation-container {
  width: 80%; 
  max-width: 400px; 
  height: auto; 
  opacity: 0;
  animation: fadeIn 1s forwards 1s; 
}

.bottom-content {
  position: absolute;
  bottom: 20px; 
  color: rgba(255, 255, 255, 0.388); 
  opacity: 0;
  animation: fadeIn 1s forwards 2s; 
}

h1 {
  font-size: 3rem; 
}

/* Responsive styles */
@media (max-width: 768px) {
  .company-name {
    font-size: 2rem; 
    font-family: 'IBM Plex Mono', monospace;

  }
  .tagline {
    font-size: 1.5rem;
  }
  h1 {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .company-name {
    font-size: 1.5rem; 
  }
  .tagline {
    font-size: 1.2rem; 
  }
  h1 {
    font-size: 1.5rem;
  }
}
